import api from '../http'

export const getAddressListForPage = (queryForm) => {
	return api.http({
		url:'/user/address/list/page',
		method:'get',
		params:queryForm
	})
}

export const insertAddress = (formData) => {
	return api.http({
		url:'/user/address/insert',
		method:'put',
		data:formData
	})
}

export const findAddressById = (id) => {
	return api.http({
		url:`/user/address/find/id/${id}`,
		method:'get',
	})
}

export const updateAddress = (formData) => {
	return api.http({
		url:'/user/address/update',
		method:'put',
		data:formData
	})
}

export const deleteAddressById = (id) => {
	return api.http({
		url:`/user/address/delete/id/${id}`,
		method:'delete',
	})
}
